.tab-list {
    width: 5%;
    height: 100%;   
    position: fixed;
    margin: 0;
    padding: 0;
    background-color: #333;
}

.tab-list-item {
    display: block;
    list-style: none;
    color: #ddd;
    padding: 2rem 0rem;
}

.tab-list-item:hover {
    cursor: pointer;
    color: #39b7e5;
    animation: glow 1s ease-in-out 0s infinite alternate;
}

.tab-list-active {
    color: #39b7e5;
    animation: glow 1s ease-in-out 0s infinite alternate;
}

.tab-content {
    padding: 1rem;
    margin-left: 5%;
    background-color: #555;
    color: #ddd;
    height: 100%;
}

@keyframes glow {
    from {
        text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #39b7e5;
      }
    to {
    text-shadow: 0 0 20px #fff, 0 0 30px #6ccef1, 0 0 40px #52c4ee;
    }
}

  
  